<template>
  <div class="cartcontrol">
    <transition name="move">
      <div class="cart-decrease" v-show="food.count>0"
         @click="decreaseCart">
          <span class="inner icon-remove_circle_outline"></span>
      </div>
    </transition>
    <div class="cart-count" v-show="food.count>0">{{food.count}}</div>
    <div class="cart-add icon-add_circle" @click="addCart">
      <i></i>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import Vue from 'vue'
export default {
  props: {
    food: {
      type: Object
    }
  },
  methods: {
    addCart () {
      if (!this.food.count) {
        Vue.set(this.food, 'count', 1)
      } else {
        this.food.count++
      }
    },
    decreaseCart () {
      if (this.food.count) {
        this.food.count--
      }
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .cartcontrol
    font-size 0
    .cart-decrease
      display inline-block
      padding 6px
      &.move-enter-active, &.move-leave-active //渐变的进入和退出都历时0.5s
        transition all 0.4s linear
        opacity 1
        transform translate3d(0,0,0)
      .inner
        display inline-block
        font-size 24px
        line-height 24px
        color rgb(0,160,220)
      &.move-enter, &.move-leave-to //定义进入前和退出后样式，即透明无色背景。
        opacity 0
        transform translate3d(24px , 0 , 0)
    .cart-count
      display inline-block
      font-size 10px
      line-height 24px
      width 12px
      vertical-align top
      padding-top:6px
      text-align center
      color rgb(147,153,159)
    .cart-add
      display inline-block
      padding 6px
      font-size 24px
      line-height 24px
      color rgb(0,160,220)
</style>



